<template>
  <div id="app">
  	<h1>在线翻译</h1>
  	<h5>简单 / 易用 / 便捷</h5>
   	 <translateForm v-on:formSubmit='translateText'></translateForm>
   	 <translateOutput v-text='translatedText'></translateOutput>
  </div>
</template>

<script>
import TranslateForm from './components/translateForm'
import TranslateOutput from './components/translateOutput'

export default {
  name: 'App',
  components: {
    TranslateForm,
    TranslateOutput
  },
  data:function(){
			return {
				translatedText:""
			}
		},
  methods:{
  	translateText:function(text){
		//alert(text);
			this.$http.get("https://translate.yandex.net/api/v1.5/tr.json/translate?key=trnsl.1.1.20180424T081240Z.25e2b820453d8bca.6ab4efc6dde0301b27ae642856d478f9c0622d20&lang=en&text="+text)
			.then((responce)=>{
//				console.log(responce.body.text[0]);
					this.translatedText = responce.body.text[0];
			})
  	}
  }
}
</script>

<style>

</style>



