<template>
	<div id="translateOutput">
		<h5>{{translatedText}}</h5>
	</div>
</template>

<script>
	export default {
		name:'translateOutput',
		props:[
			"translatedText"
		]
	}
</script>

<style>
	p{text-align: center;}
	#translateOutput{text-align: center;padding: 20px 0;}
</style>


